@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif; }

body {
  background-color: #EDEEF0; }

button {
  border: none;
  background: transparent; }

#root {
  max-width: 768px;
  margin: 0 auto; }

.card {
  margin: 0 12px;
  margin-top: 6px;
  padding: 12px;
  border-radius: 15px;
  box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
  background-color: #fff; }

.section-title {
  padding: 0 12px;
  margin-bottom: 20px;
  font-size: 18px;
  color: #818C99;
  text-transform: uppercase; }

.profiles {
  padding: 0 12px;
  display: flex;
  .items {
    padding-bottom: 15px;
    position: relative;
    display: flex;
    flex-grow: 1;
    overflow-x: scroll;
    z-index: 1; }
  .shadow {
    position: relative;
    height: 40px;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 50px;
      background-image: linear-gradient(-90deg, #EDEEF0 30%, #818C9900 100%);
      z-index: 2; } }
  .item {
    padding: 8px 20px;
    margin-right: 8px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #6D7885;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    letter-spacing: -0.154px;
    text-transform: uppercase;
    border-radius: 10px;
    white-space: nowrap; }
  .edit {
    position: relative;
    margin-left: 8px;
    padding: 6px 4px;
    background-color: #fff;
    box-shadow: 1px 2px 2px rgba(12, 12, 12, 0.1);
    svg {
      display: block; } }
  .add {
    padding: 5px 12px;
    margin-right: 35px;
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='12' ry='12' stroke='black' stroke-width='2' stroke-dasharray='6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 12px;
    svg {
      display: block; } }
  svg {
    display: none; }
  .active {
    background-color: #fff;
    box-shadow: 1px 2px 2px rgba(12, 12, 12, 0.1);
    svg {
      margin-left: 8px;
      height: .7em;
      width: .7em;
      display: block; } }
  &-menu {
    position: absolute;
    display: none;
    top: 0;
    right: 0;
    flex-direction: column;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 1px 2px 32px rgba(12, 12, 12, 0.2);
    z-index: 3; }
  .open {
    display: flex; } }

.rasp-autobus {
  margin-top: 20px;
  .autobuses {
    margin: 0 12px;
    margin-top: 6px;
    padding: 12px;
    border-radius: 15px;
    box-shadow: 0px 2px 6px rgba(12, 12, 12, 0.1);
    background-color: #fff;
    .route {
      padding-bottom: 12px;
      font-size: 20px;
      text-align: center;
      border-bottom: 1px solid #e3e3e3; }
    .direction {
      padding: 10px 0;
      border-bottom: 1px solid #e3e3e3;
      &:last-child {
        border: none; } }
    .title {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      font-size: 14px;
      svg {
        margin-right: 8px; } }
    .helt {
      padding: 8px;
      border-radius: 8px;
      background-color: #e9e9eb85;
      .title {
        margin-bottom: 8px;
        font-weight: 400; } }
    .times {
      display: flex;
      margin-bottom: 8px;
      .c-1 {
        width: 100%; }
      .c-2 {
        width: calc( 100% / 2 ); }
      .c-3 {
        width: calc( 100% / 3 ); }

      .item {
        flex-grow: 1;
        padding: 4px 8px;
        margin-right: 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.75rem;
        font-weight: 500;
        line-height: 1;
        border-radius: 6px;
        color: #000;
        background-color: #DCE1E6;
        &:last-child {
          margin-right: 0; }
        .time {
          font-size: 1.2rem; }
        .time-to {
          text-align: center; } } }
    .full-rasp {
      font-size: 14px;
      color: #2a5885; }
    .add-direction {
      padding: 8px;
      margin-top: 2px;
      margin-right: 8px;
      display: flex;
      flex-grow: 1;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 1;
      border-radius: 6px;
      color: rgb(220, 0, 78);
      border: 1px solid rgba(220, 0, 78, 0.5);
      &:last-child {
        margin-right: 0; } }
    .actions {
      margin-top: 8px;
      display: flex;
      button {
        flex-grow: 1; } } } }

#select-rasp {
  display: flex;
  flex-direction: column;
  .row {
    display: flex;
    margin-top: 12px;
    justify-content: flex-end; }
  span#or {
    margin: 0 10px;
    padding: 6px 0;
    align-self: flex-end; }
  .MuiAutocomplete-root {
    margin-top: 6px; }
  .MuiAutocomplete-inputRoot {
    padding-right: 0; }
  .MuiAutocomplete-clearIndicator {
    display: none; } }

.helt-modal {
  .direction {
    display: flex;
    padding: 8px 15px;
    margin-bottom: 5px;
    font-size: 14px;
    color: #fff;
    background-color: #42506B;
    .back {
      padding: 8px;
      margin-right: 15px; }
    .title {
      text-align: left;
      &_helt {
        padding-bottom: 4px;
        font-size: 18px; }
      &_dir {
        font-size: 12px; } } }
  .content {
    overflow: scroll;
    height: calc( 100% - 90px ); }
  .hours {
    padding: 4px 15px;
    display: flex;
    align-items: center;
    font-size: 20px;
    border-bottom: 1px solid #eee;
    .title {
      margin-right: 20px; }
    .items {
      display: flex;
      align-items: center;
      .item {
        padding: 4px;
        font-size: 14px; }
      .item.active {
        flex-grow: 1;
        padding: 4px 8px;
        margin: 0 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.75rem;
        font-weight: 500;
        line-height: 1;
        border-radius: 6px;
        color: #000;
        background-color: #DCE1E6;
        &:last-child {
          margin-right: 0; }
        .time {
          font-size: 1rem; }
        .time-to {
          text-align: center; } } } }
  .hours.active {
    background-color: #e3e2e3; } }

.modal {
  .dir-name {
    padding: 20px 15px;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 14px;
    background-color: #b6cdff;
    border-radius: 15px 15px 0 0;
    span {
      font-size: 1.2rem; } }
  .row {
    padding: 8px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee; }
  .mx-w {
    max-width: 80%;
    overflow: hidden;
    white-space: nowrap; }
  svg {
    font-size: 28px; }
  .content {
    overflow: scroll;
    height: calc( 100% - 80px ); } }

.MuiAlert-root {
  margin: 12px;
  background-color: #b6cdff !important;
  border-radius: 10px !important;
  color: #000 !important; }

.MuiDialogContent-root:first-child {
  padding: 0;
  padding-top: 0 !important; }

.MuiDialog-paper {
  margin: 0 !important;
  width: calc( 100% - 64px );
  .MuiDialogActions-root {
    button {
      &:first-child {
        color: #333; }
      &:last-child {
        font-weight: 600;
        color: #3f51b5; } } } }

.react-datepicker {
  width: 100%;
  border: none;
  overflow: hidden;
  user-select: none;

  &__custom-header {
    margin: 10px 0;
    display: flex;
    .month {
      display: flex;
      flex-grow: 1;
      align-items: center;
      justify-content: center;
      font-size: 1rem; }
    button {
      padding: 15px !important; } }
  &__header {
    padding: 0px;
    border: none;
    background-color: #fff;
    border-top-right-radius: 0 !important; }
  &__month-container {
    width: 100%; }
  &__week {
    display: flex; }
  &__week {
    .react-datepicker__day {
      &:first-child {
        border-radius: 50% 0 0 50% !important; }
      &:last-child {
        border-radius: 0 50% 50% 0 !important; } } }
  &__day {
    width: calc( 100% / 7 - 7px);
    aspect-ratio: 1 / 1;
    margin: 0;
    padding: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0 !important;
    -webkit-tap-highlight-color: transparent;
    .btn-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      aspect-ratio: 1 / 1;
      color: #000 !important;
      font-size: 1rem !important; } }
  &__day--in-range {
    background-color: #DCE1E6 !important; }
  &__day--keyboard-selected {
    background-color: #ffffff00; }
  &__day--outside-month {
    .btn-wrapper {
      color: lighten(#000, 70%) !important; } }
  .active {
    background-color: darken(#DCE1E6, 20%);
    border-radius: 50%; }
  &__day-names {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0; }
  &__day-name {
    width: calc( 100% / 7 - 7px );
    color: #00000061;
    text-align: center;
    line-height: 1;
    margin: 3px; }

  .MuiIconButton-root {
    padding: 0; } }
